  box-sizing: border-box;
  font-family: 'Outfit', sans-serif;
}

:root {
  --text-color: #4d4d4d;
  --text-color-light: #979797;
}

body {
  height: 100vh;
  display: flex;
  flex-direction: column;
  color: var(--text-color);
}

header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
}

main {
  flex: auto;
  display: flex;
  flex-direction: column;
}

#search-container {
  flex: 1;
  margin: 10px 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

#search-container > div:first-child {
  font-weight: 600;
}

#search-form {
  margin-top: 10px;
}

#location-input {
  padding: 5px 10px;
  width: 300px;
}

#location-button {
  padding: 5px 10px;
  margin-left: 5px;
}

#settings-container {
  position: absolute;
  right: 50px;
  top: 50px;
}

/* Button from https://codepen.io/alvarotrigo/pen/oNoJePo*/

.knobs,
.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.button {
  position: relative;
  top: 50%;
  width: 74px;
  height: 36px;
  margin: -20px auto 0 auto;
  overflow: hidden;
}

.button.r,
.button.r .layer {
  border-radius: 100px;
}

.checkbox {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  background-color: #ebf7fc;
  transition: 0.3s ease all;
  z-index: 1;
}
#metric-imperial-button .knobs:before {
  content: 'ºC';
  position: absolute;
  top: 4px;
  left: 4px;
  width: 20px;
  height: 10px;
  color: #fff;
  font-size: 10px;
  font-weight: bold;
  text-align: center;
  line-height: 1;
  padding: 9px 4px;
  background-color: #03a9f4;
  border-radius: 50%;
  transition: 0.3s cubic-bezier(0.18, 0.89, 0.35, 1.15) all;
}

#metric-imperial-button .checkbox:checked + .knobs:before {
  content: 'ºF';
  left: 42px;
  background-color: #f44336;
}

#metric-imperial-button .checkbox:checked ~ .layer {
  background-color: #fcebeb;
}

#metric-imperial-button .knobs,
#metric-imperial-button .knobs:before,
#metric-imperial-button .layer {
  transition: 0.3s ease all;
}

/* End Button */

/* #weather-container {
  flex: 5;
  display: none;
  grid-template-rows: 2fr 2fr 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-areas:
    'current-container forecast-24h-container'
    'current-container forecast-daily-container'
    'spacer-container spacer-container';
  gap: 20px;
} */

#weather-container {
  flex: 5;
  display: none;
  flex-direction: row;
  justify-content: center;
  padding-top: 10vh;
}

#current-container {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
  justify-content: end;
  align-items: flex-start;
  margin-right: 20px;
}

#current-container > div:last-child {
  display: flex;
}

#current-city {
  font-size: 32px;
  font-weight: 600;
}

#current-country {
  font-size: 24px;
}

#last-updated {
  color: var(--text-color-light);
  font-size: 13px;
  margin-bottom: 20px;
}

#current-weather-icon {
  width: 150px;
  height: 150px;
}

#current-weather-description {
  text-align: center;
  font-size: 18px;
}

#current-conditions {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 24px;
}

#feels-like {
  color: var(--text-color-light);
  font-size: 15px;
  margin-bottom: 10px;
  text-align: center;
}

#current-precipitation {
  margin-bottom: 10px;
}

#wind-direction {
  width: 100px;
}

#forecast-container {
  display: flex;
  flex-direction: column;
}

#forecast-24h-container {
  display: flex;
  flex-direction: column;
  justify-content: end;
}

#forecast-24h-title {
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 10px;
}

#forecast-24h-cards {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
}

.forecast-24h-card {
  display: flex;
  width: 80px;
  flex-direction: column;
  align-items: center;
  border: 1px solid var(--text-color-light);
  padding: 10px;
  border-radius: 6px;
}

.forecast-24h-time {
  margin-bottom: 5px;
}

.forecast-24h-description {
  margin-bottom: 5px;
  font-size: 12px;
  text-align: center;
}

.forecast-24h-temperature {
  margin-top: auto;
  margin-bottom: 5px;
}

#forecast-daily-container {
  margin-top: 20px;
}

#forecast-daily-cards {
  display: flex;
  gap: 10px;
}

.forecast-daily-card {
  width: 180px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.forecast-daily-title {
  font-size: 18px;
  font-weight: 600;
  align-self: flex-start;
}

.forecast-daily-date {
  align-self: flex-start;
  margin-bottom: 10px;
}

.forecast-daily-icon {
  width: 100px;
}

.forecast-daily-description {
  font-size: 14px;
  margin-bottom: 10px;
}

.forecast-daily-temperature {
  margin-bottom: 10px;
}

#loading-container {
  display: none;
  flex: 5;
  align-items: center;
  justify-content: center;
}

#error-container {
  display: none;
  align-items: center;
  justify-content: center;
  flex: 5;
}

footer {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: var(--highlight-color);
  font-family: 'Outfit', sans-serif;
  font-size: 14px;
  font-weight: 300;
}

footer > div {
  width: 250px;
}

div.author {
  display: flex;
  justify-content: center;
}

#gh-link {
  text-decoration: none;
  letter-spacing: 0;
  color: var(--font-medium);
  display: flex;
  align-items: center;
  margin: 10px 0;
  height: 38px;
}

#gh-logo {
  width: 35px;
  height: 35px;
  margin: 0 10px;
  transition: transform 0.3s ease-in-out;
}

#gh-link:hover #gh-logo {
  transform: rotate(360deg) scale(1.2);
}

html {
  visibility: visible;
  opacity: 1;
}
